.center {
  margin: left;
  width: 50%;
  height: 100%;
  /* border: 1px solid green; */
  padding: 10px;
}
.main-bg-container {
  margin-left: 300px;
  margin-top: -10px;
  width: 80vw;
  height: 100vh;
  position: right;
  padding: 50px;
  border-radius: 5px;
  position: fixed;
}

/* -----------------------------unused code----------------- */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  object-fit: cover;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bg {
  background-position: absolute;
  overflow-x: hidden;
  overflow-y: hidden;
  object-fit: cover;
  background-size: 100px;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -moz-background-size: cover;
}

